* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  height: 100%;
}

.App {
  text-align: center;
  font-family: "Shadows Into Light", cursive;
  font-weight: 400;
  font-style: normal;
}

.background {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
}

h1 {
  color: white;
  font-size: 48px;
  margin-top: 14px;
  margin-bottom: 14px;
}

.add-todo-input,
.add-todo-btn {
  height: 36px;
  background-color: transparent;
  color: white;
  border: 2px solid white;
  border-radius: 8px;
  margin: 0 4px;
  font-size: 20px;
  font-family: "Shadows Into Light", cursive;
  font-weight: 400;
  font-style: normal;
}

.add-todo-input {
  width: 160px;
  padding-left: 4px;
}

.add-todo-btn {
  width: 86px;
}

.TodoWrapper {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
}

.todo-inner-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 16px 6px auto 6px;
}

.Todo {
  display: block;
  width: 28%;
  margin: 10px 4px;
  -webkit-box-shadow: 3px 3px 10px 1px #000000;
  box-shadow: 3px 3px 10px 1px #000000;
}

.todo-task-wrapper {
  height: 95px;
  padding-top: 12px;
}

.todo-icon-wrapper {
  display: block;
  display: flex;
  justify-content: space-between;
  padding-left: 8px;
  padding-right: 8px;
  padding-bottom: 4px;
}

.completed {
  text-decoration: line-through;
}

.EditTodoForm {
  display: block;
  width: 28%;
  height: 120px;
  margin-top: 6px;
  padding: 0 2px;
}

.edit-todo-input {
  width: 100%;
  height: 24px;
  margin-top: 20px;
  margin-bottom: 2px;
  border: 1px solid black;
  border-radius: 8px;
  background-color: transparent;
  font-family: "Shadows Into Light", cursive;
  font-weight: 400;
  font-style: normal;
}

.edit-todo-btn {
  height: 24px;
  margin-top: 18px;
  border: none;
  border-bottom: 1px solid black;
  background-color: transparent;
  font-family: "Shadows Into Light", cursive;
  font-weight: 400;
  font-style: normal;
}

@media screen and (min-width: 478px) {
  .Todo, .EditTodoForm {
    margin-left: 8px;
    margin-right: 8px;
  }
}
@media screen and (min-width: 626px) {
  .Todo, .EditTodoForm {
    width: 22%;
    margin-left: 4px;
    margin-right: 4px;
  }
}
@media screen and (min-width: 768px) {
  .Todo, .EditTodoForm {
    width: 20%;
    margin-left: 8px;
    margin-right: 8px;
  }
}
@media screen and (min-width: 1024px) {
  .Todo, .EditTodoForm {
    width: 17%;
  }
}

